class Geolocation extends Component {
	constructor(props) {
	    super(props);

	    const {lat, lng} = this.props.initialCenter;

	    this.state = {
	    	isInitCoordsReceived: true, // загрузка текущего местоположения
	    	isDataFetched: false, // обработка загрузки данных
	    	isChartView: false, // показ значений акселерометра в графике
	    	data: [],
	    	currentPosition: {lat, lng, speed: 0, timestamp: new Date(Date.now()).getTime()}, 
	    	prevPosition: {lat, lng, speed: 0, timestamp: new Date(Date.now()).getTime()},
	    	currentAcceleration: {...startingAcceleration},
	    	context: null,
	    	zoom: 16,
	    	angle: 0
	    };

	    this.canvas = React.createRef();
	    this.mymap = React.createRef();
	    this.mymarker = React.createRef();

	    //if ('geolocation' in navigator) navigator.geolocation.getCurrentPosition(this.centerMap, this.err, NAVIGATOR_OPTIONS);
	}

	componentDidMount = () => {
  		const canvas = this.canvas.current;
  		this.setState({ context: canvas.getContext('2d') });

	 // 	var data;
		// db.collection("coverage").get().then(querySnapshot => {
		//     querySnapshot.forEach(function(doc) {
		//     	if (doc.data().data) {
		//     		data = JSON.parse(doc.data().data);
		//     	} else {
		//     		data = [];
		//     	}
		        
		//     });
		// }).then(() => {
		// 	this.setState({ 
		// 		context: canvas.getContext('2d'),
		// 		isDataFetched: true,
	 //  			data 
	 //  		}, () => console.log(this.state.data));
		// });


		this.setState({ // убрать при подключении Firestore
			context: canvas.getContext('2d'),
			isDataFetched: true,
			data: [] //set test for testing purposes
	 	});


	 	if (this.props.centerAroundCurrentLocation) {
	        if (navigator && navigator.geolocation) {
	            navigator.geolocation.getCurrentPosition((pos) => {
	            	console.log(pos);
	                const coords = pos.coords;
	                this.setState({
	                    currentPosition: {lat: coords.latitude, lng: coords.longitude, speed: 0, timestamp: new Date(Date.now()).getTime()}
	                })
	            })
	        }
	    }

	 	this.loadMap();
	}

	componentDidUpdate(prevProps, prevState) {
	    if (prevProps.google !== this.props.google) {
	      this.loadMap();
	    }
	    if (prevState.currentPosition !== this.state.currentPosition) {
	      this.recenterMap();
	    }
	}

  loadMap = () => {
  	if (this.props && this.props.google) {

    	var maps = this.props.google.maps;

		let {zoom} = this.props;
			//const {lat, lng} = initialCenter;
			const {lat, lng} = this.state.currentPosition;

		const center = new maps.LatLng(lat, lng);
		const mapConfig = Object.assign({}, {
	        center: center,
	        zoom: zoom
	    });

	  	this.map = new maps.Map(this.mymap, mapConfig);


	 	//this.map.on('zoomend', this.updateZoom, this);

	 	
	 	
    }
  }

  onMapReady = (mapProps, map) => {
    this.map = map;

    map.addListener('zoom_changed', this.updateZoom);
  }

  updateZoom = () => {
  	this.setState({
		zoom: this.map.getZoom()
	}, () => {console.log(this.state.zoom)})
  }

  recenterMap = () => {
  	const map = this.map;
    const curr = this.state.currentPosition;

    const google = this.props.google;
    const maps = google.maps;

    if (map) {
        let center = new maps.LatLng(curr.lat, curr.lng)
        map.panTo(center);
    }
  }


	// centerMap = location => {

	// 	 var lat = location.coords.latitude;
	// 	 var lng = location.coords.longitude;
	// 	 var timestamp = location.timestamp;

	// 	 timestamp = Date.now(); //только для тестирования

		
	// 	this.setState({
	// 		currentPosition: { lat,	lng, speed: 0,	timestamp },
	// 		isInitCoordsReceived: true
	// 	});

		// const map = this.map;
	 //    const current = this.state.currentLocation;

	 //    const google = this.props.google;
	 //    const maps = google.maps;

	 //    if (map) {
	 //      let center = new maps.LatLng(current.lat, current.lng);
	 //      map.panTo(center);
	 //    }
	//}

	startRecording = () => {
		if ('geolocation' in navigator) { // обработка отсутствия
 	    	navigatorId = navigator.geolocation.watchPosition(this.updatePositionData, this.err, NAVIGATOR_OPTIONS);
	    	if (window.DeviceMotionEvent) { //обработка отсутствия
	    		window.addEventListener("devicemotion", this.updateAccelerationData); 
	    	}
	    }	   
	}

	err = () => {
		console.log("error");
	}

	updateAccelerationData = (ev) => {
		let x = ev.acceleration.x;
		let y = ev.acceleration.y;
		let z = ev.acceleration.z;

		if (x !== null && y !== null && z !== null) {
			this.setState({
				currentAcceleration: { x, y, z}
			}, () => {
				if (line === this.canvas.current.width) line = 0;
				
				this.drawBackground("white", 0);

				var index = 1;
				let accel = this.state.currentAcceleration;
				for (var prop in accel) {
					this.drawLine(accel[prop], index);
					index++;
				}
				line++;

				this.drawBackground("grey", 1);
				for (var i = 2; i < 8; i++) this.drawBackground("white", i);
			})
		}
	}

	toRad = num => {
		return num * Math.PI/180;
	}

	calculateDistance = (lat1, lng1, lat2, lng2) => {
		var dLat = this.toRad(lat2 - lat1);
		var dLng = this.toRad(lng2 - lng1);

		var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);

		var distance = EARTH_RADIUS * 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
		//console.log(distance); // в метрах
		return distance;
	}

	calculateAngle = (lat1, lng1, lat2, lng2) => {

	    var dLng = (lng2 - lng1);

	    var y = Math.sin(dLng) * Math.cos(lat2);
	    var x = Math.cos(lat1) * Math.sin(lat2) - Math.sin(lat1) * Math.cos(lat2) * Math.cos(dLng);

	    var brng = Math.atan2(y, x);

	    var pi = Math.PI;
	    brng = brng * (180/pi);
	    brng = (brng + 360) % 360;
	    brng = 360 - brng; // count degrees counter-clockwise - remove to make clockwise

	    return brng;

	}

	drawLine = (value, index) => {
		let ctx = this.state.context;
		var axis = index * 100 - 50;

		ctx.beginPath();
		ctx.strokeStyle="black";
		ctx.moveTo(line, axis + value * 5);
		ctx.lineTo(line, axis);
		ctx.stroke();
	}

	drawBackground = (color, offset) => {
		let ctx = this.state.context;

		ctx.beginPath();
		ctx.strokeStyle = color;
		ctx.moveTo(line + offset, 0);
		ctx.lineTo(line + offset, 300);
		ctx.stroke();
	}

	updatePositionData = (location) => {
		//var test = {...this.state.currentPosition};
		

		this.setState({
			prevPosition: {...this.state.currentPosition}
		}, () => {
			var prev = this.state.prevPosition;

			//console.log("prev");
			

			var lat = location.coords.latitude;
			var lng = location.coords.longitude;
			var timestamp = location.timestamp;

			timestamp = Date.now();

			//console.log(this.state.prevPosition);

			var distance = this.calculateDistance(prev.lat, prev.lng, lat, lng); // в метрах
			var angle = this.calculateAngle(prev.lat, prev.lng, lat, lng);
			var time = (prev.timestamp - timestamp)/ 1000; // в секундах
			var speed = Math.abs(distance/time);

			this.setState({	
				currentPosition: { lat,	lng, speed,	timestamp },
				data: [...this.state.data, { lat, lng }],
				angle 
			}, () => {
				//console.log(this.state.data);
				//console.log("after");
				//console.log(this.state.currentPosition);
			});
		});		
	}

	updateResultingData = () => {
		
	}

	stopRecording = () => {
		console.log("stopped");
		console.log(this.state.data);


		//db.collection("coverage").doc(topleft).set({data: JSON.stringify(this.state.data)});

		navigator.geolocation.clearWatch(navigatorId);
		window.removeEventListener("devicemotion", this.updateAccelerationData);
	}

	showChart = () => {
		this.setState({isChartView: !this.state.isChartView});
	}

	  render() {
	  	let google = this.props.google;
	  	let zoom = this.state.zoom  ;
	  	
	  	if (this.state.isInitCoordsReceived) {
	    return (
	    <div>
	    	<button onClick={this.startRecording} disabled={!this.state.isDataFetched}>Start recording</button>
	    	<button onClick={this.showChart}>Show chart</button>
	    	<div>lat={this.state.currentPosition.lat}</div>
	    	<div>lng={this.state.currentPosition.lng}</div>
	    	<div>speed={this.state.currentPosition.speed}</div>
	    	<canvas id="canvas" width="400" height="300" ref={this.canvas} style={{display: this.state.isChartView ? "block" : "none"}}/>
	    	<div>{this.state.currentPosition.lat}</div>

	    	<div id="map" style={{width: "400px", height: "300px", position: "relative"}}>
	    		<Map 
	    			centerAroundCurrentLocation
	    			ref={this.mymap} 
	    			google={this.props.google}
		          	style={{width: "400px", height: "300px", position: "relative"}} 
		          	onReady={this.onMapReady}
		          	>
		          	<Marker
		          		ref={this.mymarker}
						position={{lat: this.state.currentPosition.lat, lng: this.state.currentPosition.lng}}
    					icon={{
    						path: 'M 23.5 7 c 0.276 0 0.5 0.224 0.5 0.5 v 0.511 c 0 0.793 -0.926 0.989 -1.616 0.989 l -1.086 -2 h 2.202 Z m -1.441 3.506 c 0.639 1.186 0.946 2.252 0.946 3.666 c 0 1.37 -0.397 2.533 -1.005 3.981 v 1.847 c 0 0.552 -0.448 1 -1 1 h -1.5 c -0.552 0 -1 -0.448 -1 -1 v -1 h -13 v 1 c 0 0.552 -0.448 1 -1 1 h -1.5 c -0.552 0 -1 -0.448 -1 -1 v -1.847 c -0.608 -1.448 -1.005 -2.611 -1.005 -3.981 c 0 -1.414 0.307 -2.48 0.946 -3.666 c 0.829 -1.537 1.851 -3.453 2.93 -5.252 c 0.828 -1.382 1.262 -1.707 2.278 -1.889 c 1.532 -0.275 2.918 -0.365 4.851 -0.365 s 3.319 0.09 4.851 0.365 c 1.016 0.182 1.45 0.507 2.278 1.889 c 1.079 1.799 2.101 3.715 2.93 5.252 Z m -16.059 2.994 c 0 -0.828 -0.672 -1.5 -1.5 -1.5 s -1.5 0.672 -1.5 1.5 s 0.672 1.5 1.5 1.5 s 1.5 -0.672 1.5 -1.5 Z m 10 1 c 0 -0.276 -0.224 -0.5 -0.5 -0.5 h -7 c -0.276 0 -0.5 0.224 -0.5 0.5 s 0.224 0.5 0.5 0.5 h 7 c 0.276 0 0.5 -0.224 0.5 -0.5 Z m 2.941 -5.527 s -0.74 -1.826 -1.631 -3.142 c -0.202 -0.298 -0.515 -0.502 -0.869 -0.566 c -1.511 -0.272 -2.835 -0.359 -4.441 -0.359 s -2.93 0.087 -4.441 0.359 c -0.354 0.063 -0.667 0.267 -0.869 0.566 c -0.891 1.315 -1.631 3.142 -1.631 3.142 c 1.64 0.313 4.309 0.497 6.941 0.497 s 5.301 -0.184 6.941 -0.497 Z m 2.059 4.527 c 0 -0.828 -0.672 -1.5 -1.5 -1.5 s -1.5 0.672 -1.5 1.5 s 0.672 1.5 1.5 1.5 s 1.5 -0.672 1.5 -1.5 Z m -18.298 -6.5 h -2.202 c -0.276 0 -0.5 0.224 -0.5 0.5 v 0.511 c 0 0.793 0.926 0.989 1.616 0.989 l 1.086 -2 Z',
    						scaledSize: new google.maps.Size(zoom, zoom),
							rotation: this.state.angle
    					}}
					/>
					<Polyline
			          path={this.state.data}
			          strokeColor="#0000FF"
			          strokeOpacity={0.8}
			          strokeWeight={2} 
			          />
					</Map>
	    	</div>
	    	
	    	<img src={car} alt="car" />
	    	<button onClick={this.stopRecording}>Stop recording</button>
	    </div>
	    );
	} else {
		return (
			<div>
			<canvas id="canvas" width="400" height="300" ref={this.canvas} style={{display: this.state.isChartView ? "block" : "none"}}/>
			 <div>Getting current position...</div>
			 </div>
		);
	  }
	}
}

